    to right bottom,
    rgba(74, 122, 184, 0.85),
    rgba(36, 50, 114, 0.85)
  );
}
.card__heading-span--4 {
  background-image: linear-gradient(
    to right bottom,
    rgba(253, 193, 104, 0.85),
    rgba(251, 128, 128, 0.85)
  );
}
.card__heading-span--5 {
  background-image: linear-gradient(
    to right bottom,
    rgba(204, 183, 145, 0.85),
    rgba(223, 171, 74, 0.85)
  );
}
.card__heading-span--6 {
  background-image: linear-gradient(
    to right bottom,
    rgba(104, 206, 197, 0.85),
    rgba(32, 158, 148, 0.85)
  );
}
.card__details ul {
  list-style: none;
  width: 80%;
  margin: 0 auto;
}
.card__details ul li {
  text-align: center;
  font-size: 1.5rem;
  padding: 1rem;
}
.card__details ul li:not(:last-child) {
  border-bottom: 1px solid #eee;
}
.card__cta {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 90%;
  text-align: center;
}
.card__price-box {
  text-align: center;
  margin-bottom: 8rem;
  color: white;
}
.card__price-only {
  font-size: 1.4rem;
}
.card__price-value {
  font-size: 6rem;
  font-weight: 300;
}
@media (max-width: 56.25em) {
  .card {
    height: auto;
    border-radius: 3px;
    background-color: #fff;
    box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15);
    overflow: hidden;
  }
}
@media (max-width: 56.25em) and (max-width: 37.5em) {
  .card {
    margin: 1rem;
  }
}
@media (max-width: 56.25em) {
  .card__side {
    height: auto;
    position: relative;
    box-shadow: none;
  }
  .card__side--back {
    transform: rotateY(0);
    clip-path: polygon(0 15%, 100% 0, 100% 100%, 0 100%);
  }
  .card:hover .card__side--front {
    transform: none;
  }
  .card__cta {
    position: relative;
    top: 0;
    left: 0;
    transform: translate(0);
    width: 90%;
    text-align: center;
    padding: 10rem 0 5rem 0;
    margin: 0 auto;
  }
  .card__price-box {
    margin-bottom: 4rem;
  }
  .card__price-only {
    font-size: 1.4rem;
  }
  .card__price-value {
    font-size: 6rem;
  }
}

.story {
  width: 75%;
  margin: 0 auto;
  box-shadow: 0 3rem 6rem rgba(0, 0, 0, 0.1);
  background-color: rgba(255, 255, 255, 0.6);
  border-radius: 0.5rem;
  padding: 6rem;
  padding-left: 9rem;
  font-size: 1.6rem;
}
@media (max-width: 37.5em) {
  .story {
    padding: 3rem;
    padding-left: 4.5rem;
    width: 75%;
  }
}
.story__shape {
  width: 15rem;
  height: 15rem;
  float: left;
  overflow: hidden;
  position: relative;
  -webkit-shape-outside: circle(50% at 50% 50%);
  shape-outside: circle(50% at 50% 50%);
  clip-path: circle(50% at 50% 50%);
  -webkit-clip-path: circle(50% at 50% 50%);
  transform: translateX(-3rem);
}
.story__image {
  height: 100%;
  transform: scale(1.4);
  transition: all 0.5s;
  backface-visibility: hidden;
}
.story__caption {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 50%);
  color: #fff;
  text-transform: uppercase;
  font-size: 1.7rem;
  text-align: center;
  opacity: 0;
  transition: all 0.5s;
  backface-visibility: hidden;
}
.story:hover .story__caption {
  opacity: 1;
  transform: translate(-50%, -50%);
}
.story:hover .story__image {
  transform: scale(1);
  filter: blur(3px) brightness(80%);
}

.bg-video {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  opacity: 0.15;
  z-index: -1;
  overflow: hidden;
}
.bg-video__content {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.form__group:not(:last-child) {
  margin-bottom: 2rem;
}

.form__input {
  font-size: 1.5rem;
  font-family: inherit;
  padding: 1.5rem 2rem;
  border-radius: 2px;
  background-color: rgba(255, 255, 255, 0.5);
  border: none;
  border-bottom: 3px solid transparent;
  width: 70%;
  display: block;
}
.form__input:focus {
  outline: none;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.1);
  border-bottom: 3px solid #464646;
}

.section-about {
  padding: 25rem 0 50rem 0;
  margin-top: -20vh;
}
@media (max-width: 56.25em) {
  .section-about {
    padding: 20rem 0 25rem 0;
  }
}
@media (max-width: 37.5em) {
  .section-about {
    padding: 17.5rem 0 27.5rem 0;
  }
}
@media screen and (max-width: 37.5em) and (min-height: 900px) {
  .section-about {
    padding: 30rem 0 30rem 0;
  }
}

.section-projects {
  padding: 20rem 0;
  background-image: linear-gradient(
    to right bottom,
    rgba(51, 51, 51, 0.6),
    #6d6d6d
  );
  background-size: cover;
  background-position: top;
  position: relative;
  transform: skewY(-7deg);
  margin-top: -15rem;
}
.section-projects > * {
  transform: skewY(7deg);
}
@media (max-width: 37.5em) {
  .section-projects {
    padding: 10rem 0;
  }
}

.section-tours {
  padding: 25rem 0 25rem 0;
  margin-top: -15rem;
}
@media (max-width: 56.25em) {
  .section-tours {
    padding: 20rem 0 10rem 0;
  }
}

.section-stories {
  padding: 15rem 0;
  position: relative;
  background-image: linear-gradient(
      to right bottom,
      rgba(109, 109, 109, 0.6),
      rgba(51, 51, 51, 0.6)
    ),
    url("../img/small-intro-bg-1.jpg");
  background-size: cover;
}
@media (max-width: 56.25em) {
  .section-stories {
    padding: 5rem 0;
  }
}

.section-contact {
  padding: 12rem 0;
  background-color: #f7f7f7;
}

.contact-me {
  text-align: center;
  font-size: 1.6rem;
}
.contact-me *:not(:last-child) {
  margin-bottom: 2.5rem;
}
.contact-me__phone-section,
.contact-me__email-section {
  box-shadow: 0 0.5rem 1.5rem rgba(0, 0, 0, 0.4);
  padding: 4rem 2rem;
  width: 30rem;
  display: inline-block;
  margin: 5rem;
  transition: all 0.2s;
  border-radius: 50rem;
}
@media (max-width: 56.25em) {
  .contact-me__phone-section,
  .contact-me__email-section {
    margin: 1.5rem;
  }
}
.contact-me__phone-section:hover,
.contact-me__email-section:hover {
  transform: scale(1.1);
}
.contact-me__label {
  text-transform: uppercase;
}
@media (max-width: 56.25em) {
  .contact-me__text {
    margin-bottom: 0 !important;
  }
}
